html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

nav{
  background: #2C323C;
  font-size: 20;
  color: white;
}

.resetGame{
  float: right;
}

.alertbox {
  background: rgba(255, 204, 204, 1);
}

.alert-shown {
  opacity: 1;
  transition: all 250ms linear;
  font-size: 2rem;
  background: rgba(255, 204, 204, 1);
}

.alert-hidden {
  opacity: 0;
  transition: all 250ms linear;
  font-size: 2rem;
  background: rgba(255, 204, 204, 1);
}

.wrapper {
  padding-top: 50px;
  background: #ffcccc;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  font-size: 3rem;
  color: white;
  background: #2C323C;
  float: left;
}


.img-container:hover{
  transform: scale(1.1);
}
.img-container {
  height: 325px;
  width: 325px;
  position: relative;
  overflow: hidden;
  text-align: center;
  background:#ffcccc;
}
.img-container > img {
  width: 60%
}
